<template
	><div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')" class="current">유튜브</li>
			<li @click="goto('/admin/column')">칼럼</li>
			<li @click="goto('/admin/writer')">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<div class="tab-contents">
				<ul class="table board">
					<li class="row props">
						<div class="prop check">
							<input
								type="checkbox"
								class="checkbox"
								name="member"
								id="select-all"
								v-model="selectAll"
							/>
							<label for="select-all"></label>
						</div>
						<div class="prop">대표 이미지</div>
						<div class="prop small">제목</div>
						<div class="prop middle">내용</div>
						<div class="prop">작성일자</div>
						<div class="prop">유튜브 링크</div>
						<div class="prop">수정</div>
					</li>
					<slot v-if="!youtubeList">
						<li class="row no-data body3" style="display: block;">
							등록된 글이 없습니다
						</li>
					</slot>
					<slot v-else>
						<li v-for="item in youtubeList" :key="item.id" class="row">
							<div class="prop check">
								<input
									type="checkbox"
									class="checkbox"
									name="member"
									:id="item.id"
									:value="item.id"
									v-model="selected"
								/>
								<label :for="item.id"></label>
							</div>
							<div class="prop image">
								<img :src="item.imgPAth" width="100" height="70" />
							</div>
							<div class="prop small txt-left">
								{{ item.title }}
							</div>
							<div class="prop middle txt-left">
								{{ item.contents }}
							</div>
							<div class="prop ">
								{{ $moment(item.updateDate).format('YYYY.MM.DD') }}
							</div>
							<div class="prop">
								<a :href="item.link" target="_blank"><Link /></a>
							</div>
							<div class="prop">
								<router-link
									:to="{ name: 'EditYoutube', params: { id: item.id } }"
									><Edit
								/></router-link>
							</div>
						</li>
					</slot>
				</ul>
				<div class="bottom-container">
					<Pagination
						v-if="totalPage !== -1"
						:listRowCt="15"
						:pageLinkCt="5"
						:totalCount="totalPage"
						:url="`/admin/youtube`"
					/>
					<div class="btn-container">
						<div class="checked-info body3 marR-30">
							<span class="number">{{ selectedCnt }}</span
							><span>개 선택됨</span>
						</div>

						<button
							class="solid-btn red"
							@click="isDeleteModalViewed = true"
							:disabled="selected.length < 1"
						>
							선택 글 삭제
						</button>
						<router-link :to="{ name: 'AddYoutube' }">
							<button class="solid-btn green" type="button">
								새 글 등록
							</button>
						</router-link>
					</div>
				</div>
				<ModalView
					v-if="isDeleteModalViewed"
					@close-modal="isDeleteModalViewed = false"
				>
					<DeleteItem
						@close-modal="isDeleteModalViewed = false"
						@confirm="deleteYoutube"
						:count="selected.length"
					></DeleteItem>
				</ModalView>
			</div>
		</div>
	</div>
</template>
<script>
import {
	fetchYoutube,
	deleteYoutube,
	fetchYoutubeTotalCount,
} from '@/api/youtube';
import Link from '@/assets/icon/Link';
import Edit from '@/assets/icon/Edit';
import Pagination from '@/components/common/Pagination';
import DeleteItem from '@/views/admin/notice-news/components/DeleteItem';
import ModalView from '@/components/common/ModalView';

export default {
	components: { DeleteItem, Edit, Link, ModalView, Pagination },
	created() {
		this.currentPage = this.$route.params.page;

		this.fetchYoutube(this.currentPage);
		this.fetchYoutubeTotalCount();
	},
	data() {
		return {
			youtubeList: [],
			selected: [],
			totalPage: -1,
			currentPage: 1,
			isLoading: false,
			isDeleteModalViewed: false,
		};
	},
	computed: {
		selectedCnt() {
			return this.selected.length;
		},
		selectAll: {
			get: function() {
				return this.youtubeList
					? this.selected.length == this.youtubeList.length
					: false;
			},
			set: function(value) {
				var selected = [];

				if (value) {
					this.youtubeList.forEach(function(item) {
						console.log(item.id);
						selected.push(item.id);
					});
				}
				this.selected = selected;
			},
		},
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchYoutube(page) {
			this.isLoading = true;
			try {
				const { data } = await fetchYoutube(page - 1);
				console.log(page);
				this.youtubeList = data;
			} catch (e) {
				alert(e);
			}
		},
		async fetchYoutubeTotalCount() {
			this.isLoading = true;
			try {
				const { data } = await fetchYoutubeTotalCount();
				this.totalPage = data.totalCount;
			} catch (e) {
				alert(e);
			}
		},
		async deleteYoutube() {
			this.isLoading = true;
			try {
				await deleteYoutube(this.selected);
				this.fetchYoutube(this.currentPage);
				this.isDeleteModalViewed = false;
				this.selected = [];
			} catch (e) {
				alert(e);
			}
		},
	},
	watch: {
		$route(to, from) {
			if (to !== from) {
				this.currentPage = this.$route.params.page;
				this.fetchYoutube(this.currentPage);
			}
		},
	},
};
</script>
<style scoped>
.small {
	width: 200px;
}
.middle {
	width: 283px;
}
</style>
